import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Cards from './Cards';
const Movies = () => {



  const [res, setRes] = useState([]);
  const [page, setpage] = useState(1);
  const fetchMovies = async () => {
    // const { data } = await axios.get(`https://api.themoviedb.org/3/trending/all/day?api_key=${process.env.REACT_APP_API_KEY}`);
    const { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}&with_watch_monetization_types=flatrate`);
    setRes(data.results)
  }

  //handle previous button
  const handlePrev = async () => {

    setpage(page - 1);
    const { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}&with_watch_monetization_types=flatrate`);
    setRes(data.results);

    window.scroll(0, 0);
  }


  //handle next button
  const handleNext = async () => {

    setpage(page + 1);
    const { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}&with_watch_monetization_types=flatrate`);
    setRes(data.results);
    window.scroll(0, 0);
  }
  useEffect(() => {
    fetchMovies();
    // eslint-disable-next-line
  }, [page])
  return (
    <>
      <div className='app'> 
        <span className='trending'>DISCOVER MOVIES </span>
        <div className='movieList'>
          {res && res.map((ele) => {
            return <div key={ele.id}>
              <Cards img={ele.poster_path ?` https://image.tmdb.org/t/p/w300${ele.poster_path}` : 'https://upload.wikimedia.org/wikipedia/en/6/60/No_Picture.jpg'} title={ele.title || ele.name} type="movie" date={ele.release_date || ele.first_air_date} rating={ele.vote_average} id={ele.id} />
            </div>
          })}
        </div>
        <div className='buttons'>
          {page !== 1 ?
            <>
              <button className='btn' id='prevBtn' onClick={handlePrev}>&#60; Previous</button>
              <span className='pageMark'>Page {page}</span>
            </>
            :
            ''}

          <button className='btn' id='nextBtn' onClick={handleNext}>Next &#62;</button>
        </div>
      </div>
    </>
  )
}
export default Movies;
